// import React, { useState } from 'react';
import React from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import { TCartItem } from '../../redux/slices/cart/types';
import { addItem } from '../../redux/slices/cart/slice';
import { selectorCartItemById } from '../../redux/slices/cart/selectors';

const typeNames = ['', ''];

type PizzaBlockProps = {
	id: string;
	title: string;
	price: number;
	imageUrl: string;
	sizes: number[];
	types: number[];
};

export const PizzaBlock: React.FC<PizzaBlockProps> = ({
	id,
	title,
	price,
	imageUrl,
	sizes,
	types,
}) => {
	// function PizzaBlock(props) {

	//      
	const cartItem = useSelector(selectorCartItemById(id));
	// const cartItem = useSelector(state =>  selectorCartItemById(id, state));

	// console.log(cartItem )

	const addedCount = cartItem ? cartItem.count : 0;
	// console.log(addedCount)
	//   (pizzaCount), ,    (setPizzaCount),         (useState(0)).     .

	// const [pizzaCount, setPizzaCount] = useState(0);
	//       ,  :
	// import React from 'react';
	//  ,     ,      ,     
	// const [pizzaCount, setPizzaCount] = React.useState(0);

	//    onClickBtn  button   ,    :  (setPizzaCount),  pizzaCount,  pizzaCount    1.
	// const onClickBtn = () => {
	// 	setPizzaCount(pizzaCount + 1);
	// };

	const [activeType, setActiveType] = React.useState(0);
	const onClickType = (index: number) => {
		setActiveType(index);
	};
	const [activeSize, setActiveSize] = React.useState(0);

	const dispatch = useDispatch();

	const onClickAddItem = () => {
		const item: TCartItem = {
			id,
			title,
			price,
			imageUrl,
			size: sizes[activeSize],
			type: typeNames[activeType],
			count: 0,
		};
		dispatch(addItem(item));
	};

	return (
		<div className="pizza-block-wrapper">
			<div className="pizza-block">
				<Link
					key={id}
					to={`/pizza/${id}`}
				>
					<img
						className="pizza-block__image"
						src={imageUrl}
						alt="Pizza"
					/>

					<h4 className="pizza-block__title">{title}</h4>
				</Link>
				{/* 		<h4 className="pizza-block__title">{props.title}</h4> */}
				<div className="pizza-block__selector">
					<ul>
						{types.map((type, index) => {
							return (
								<li
									key={type}
									onClick={() => onClickType(index)}
									className={activeType === index ? 'active' : ''}
								>
									{typeNames[type]}
								</li>
							);
						})}

						{/* <li className="active"></li>
					<li></li> */}
					</ul>
					<ul>
						{/*    sizes,       PizzaBlock(),     map,     sizes      li   ,    sizes. */}
						{sizes.map((size, index) => {
							return (
								<li
									key={size}
									onClick={() => setActiveSize(index)}
									className={activeSize === index ? 'active' : ''}
								>
									{size} .
								</li>
							);
						})}
					</ul>
				</div>
				<div className="pizza-block__bottom">
					<div className="pizza-block__price"> {price} </div>
					{/* 				<div className="pizza-block__price"> {props.price} </div> */}
					<button
						//      onClick-   (   onClickBtn),    
						onClick={onClickAddItem}
						className="button button--outline button--add"
					>
						<svg
							width="12"
							height="12"
							viewBox="0 0 12 12"
							fill="none"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
								fill="white"
							/>
						</svg>
						<span></span>
						{/*      */}
						{/* <i>{pizzaCount}</i> */}
						{/*    {}   */}
						{addedCount > 0 && <i>{addedCount}</i>}
					</button>
				</div>
			</div>
		</div>
	);
};
