import React from 'react';
import { useSelector } from 'react-redux';

import Pagination from '@mui/material/Pagination';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Stack from '@mui/material/Stack';

// import Typography from '@mui/material/Typography';

const theme = createTheme({
	palette: {
		primary: {
			main: '#fe5f1e',
		},
	},
});

type PaginationBlockProps = { onChangePage: (page: number) => void };
// onChangePage    ,     page,   number    
//     ,    .   ?  :
// type PaginationBlockProps = { onChangePage: (page?: number) => void };

export const PaginationBlock: React.FC<PaginationBlockProps> = ({ onChangePage }) => {
	//    value    ,    '@mui/material/styles',       Pagination

	const handleChange = (event: React.ChangeEvent<unknown>, value: number) => {
		onChangePage(value);
	};
	const { currentPage } = useSelector((state: { filter: any }) => state.filter);
	// console.log(currentPage)
	return (
		<div>
			<Stack spacing={2}>
				{/* <Typography>Page: {currentPage}</Typography> */}
				<ThemeProvider theme={theme}>
					<Pagination
						//        ,     ,           (  "limit=")    (fetch).
						count={3}
						color="primary"
						page={currentPage}
						//   material ui  onChange   Pagination    2 : 1 - event, 2 - value ( ,   .)
						//        setCurrentPage   
						onChange={handleChange}
						// onChange={(event, value) => {
						// 	console.log(value);
						// }}
					/>
				</ThemeProvider>
			</Stack>
		</div>
	);
};


